function categorychange(type) {
  var type = type;
  if (type == 1) {
    type = "PPT";
  } else if (type == 2) {
    type = "UI";
  } else if (type == 3) {
    type = "文本画册";
  } else if (type == 4) {
    type = "海报展览";
  } else if (type == 5) {
    type = "LOGO";
  } else if (type == 6) {
    type = "企业形象设计（VI）";
  } else if (type == 0) {
    type = "测试品类";
  }
  return type;
}

